import UndoButton from './components/undoButton';
import RedoButton from './components/redoButton';
import type { HistoryPluginConfig } from './types';

export default function toolbar(historyConfig: HistoryPluginConfig) {
  const historyLocaleConfig = historyConfig.locale || {};

  return {
    buttons: {
      undo: historyConfig.UndoButton || UndoButton,
      redo: historyConfig.RedoButton || RedoButton,
    },
    locale: historyLocaleConfig,
  };
}
